
* {
  font-family: 'Comfortaa', cursive;
  color: #222;
}
html{
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(226,226,226,1)), color-stop(49%, rgba(214,214,214,1)), to(rgba(254,254,254,1))) no-repeat center center fixed;
  background: -webkit-linear-gradient(top, rgba(226,226,226,1) 0%, rgba(214,214,214,1) 49%, rgba(254,254,254,1) 100%) no-repeat center center fixed;
  background: -o-linear-gradient(top, rgba(226,226,226,1) 0%, rgba(214,214,214,1) 49%, rgba(254,254,254,1) 100%) no-repeat center center fixed;
  background: linear-gradient(to bottom, rgba(226,226,226,1) 0%, rgba(214,214,214,1) 49%, rgba(254,254,254,1) 100%) no-repeat center center fixed;
  background-size: cover;
  min-height: 100%;
  margin: 0;
  padding: 0;
}
body{
  margin: 0 0 100px;
  padding: 0;
}
.app-container {
  text-align: center;
}
.sequence-title {
  text-transform: uppercase;
  font-size: .9rem;
  height: 100px;
  vertical-align: middle;
  line-height: 100px;
  margin: 0;
  padding: 0;
  -webkit-animation: slideDown 1s ease-in;
          animation: slideDown 1s ease-in;
}

@-webkit-keyframes slideDown {
	from {
		-webkit-transform: translate3d(0, -100px, 0);
		        transform: translate3d(0, -100px, 0);
	}
  to {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

@keyframes slideDown {
	from {
		-webkit-transform: translate3d(0, -100px, 0);
		        transform: translate3d(0, -100px, 0);
	}
  to {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}



