.steps{
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  overflow: hidden;
  position: relative;
  height: calc(100vh - 200px);
  margin: 0 auto;
  vertical-align: middle;
}
.steps-container{
  position: absolute;
  left: 0;
  -webkit-transition: 1s all;
  -o-transition: 1s all;
  transition: 1s all;
}



